import './App.css';
import MealForm from './MealForm';
import MealList from './MealList';
import WeeklyMeals from './WeekMeals';
import * as React from "react"
import {useState} from "react"
import { Box, ChakraProvider, Flex, HStack, Heading, SimpleGrid, Spacer, VStack } from '@chakra-ui/react';
import TopBar from './topBar';
import { Meal } from './Meal';

function App() {
  const [meals, setMeals] = useState<Meal[]>(() => {
    // getting stored value
    const saved = localStorage.getItem("savedMeals");
    if (saved == null) { // Type guard against null
      return []
    }
    const initialValue = JSON.parse(saved);
    return initialValue || [];
  });

  const addMeal = (meal) => {
    const newMeals = [...meals, meal]
    setMeals(newMeals)
    localStorage.setItem("savedMeals", JSON.stringify(newMeals));
  }

  const removeMeal = (meal) => {
    // Should create a new array not modify in place.
    const filteredMeals = meals.filter((m) => m !== meal)
    setMeals(filteredMeals)
    localStorage.setItem("savedMeals", JSON.stringify(filteredMeals));
  }

  return (
    <ChakraProvider>
      {/* <VStack> */}
      <Box>
        <TopBar weekMeals={meals}></TopBar>
        <Box padding={'4'}>
          <WeeklyMeals mealList={meals}/>
        </Box>
          <SimpleGrid columns={2} minChildWidth='350px' spacing='10px' padding={'3'} textAlign={'center'}>      
            {/* <Flex textAlign={'center'} justify='center' width='70%' padding={'5'}> */}
              <Box border={'thin solid'} borderColor={'#65B741'} borderRadius={'30px'} p={'5'}>
                <MealForm onAddMeal={addMeal}/>
              </Box>
              <Spacer/>
              <Box border={'thin solid'} borderColor={'#65B741'} borderRadius={'30px'} p={'5'}>
                <MealList currentMeals={meals} removeMeal={removeMeal}/>
              </Box>
            {/* </Flex> */}
          </SimpleGrid>
          </Box>
      {/* </VStack> */}
    </ChakraProvider>
  );
}

export default App;
