import * as React from "react"
import {useState} from "react"
import { Button, Heading, Input, Textarea, Spacer, Text, VStack, Accordion, AccordionItem, Box, AccordionButton, AccordionIcon, AccordionPanel } from '@chakra-ui/react'

function MealForm({ onAddMeal }) {
  const [name, setName] = useState('');
  const [calories, setCalories] = useState('')
  const [description, setDescription] = useState('');

  const handleSubmit = (e) => {
    e.preventDefault();
    onAddMeal( {name: name, description: description, calories: calories}); // could be {name, description}
    setName('');
    setDescription('');
    setCalories('');
  };

  return (
    <Box>
    <form onSubmit={handleSubmit}>

      <Heading>Add Meal</Heading>
      <Spacer p={2}/>
      <VStack>
        <Input
          type="text"
          id="name"
          placeholder='Meal name'
          value={name}
          onChange={(e) => setName(e.target.value)}
        />
        {/* <Textarea
        placeholder='TODO: Refactor to form, has description. Have button that lets you extend if you want.'
          id="description"
          value={description}
          onChange={(e) => setDescription(e.target.value)}
        /> */}
        <Accordion allowToggle w={'100%'}>
          <AccordionItem>
            <h2>
              <AccordionButton>
                <Box as="span" flex='1' textAlign='left'>
                  Optional Descriptors
                </Box>
              <AccordionIcon />
              </AccordionButton>
            </h2>
            <AccordionPanel>
              <Input
                type='text'
                id='calories'
                placeholder='Calories'
                value={calories}
                onChange={(e) => setCalories(e.target.value)}
              />
              <Textarea
                placeholder='Desciption of recipe'
                  value={description}
                  onChange={(e) => setDescription(e.target.value)}
              />
            </AccordionPanel>
          </AccordionItem>
        </Accordion>
        <Button type="submit">Add Meal</Button>
      </VStack>
    </form>
    </Box>
  );
}

export default MealForm;