import * as React from "react"
import { Button, Heading, Table, TableContainer, Td, Thead, Tbody, Th, Tr, Box, Spacer, Text, Menu, MenuButton, MenuList, MenuItem, } from '@chakra-ui/react'
import { ChevronDownIcon } from '@chakra-ui/icons'

function MealForm({currentMeals, removeMeal}) {
  return (
    <Box
    // p={'5'}
    // boxShadow={'16px 16px 23px #a1a1a1,-16px -16px 23px #ffffff;'}
    // borderRadius='20px'
    >
            <Heading>All Meals</Heading>
            <Spacer p={3}/>
    {currentMeals.length > 0 ?
        <TableContainer overflowY={'auto'} maxHeight={'280px'}>
        <Table variant='simple'>
        <Thead>
            <Tr>
                <Th>Meal Name</Th>
                {/* <Th>Description</Th> */}
                <Th>Options</Th>
            </Tr>
        </Thead>
        <Tbody>
            {currentMeals.map((meal) => (
                <Tr>
                    <Td>
                        {meal.name}
                    </Td>
                    {/* <Td>
                        Need a maximum length on this one, otherwise it can blow out.
                        {meal.description}
                        <Button>Placeholder</Button>
                    </Td> */}
                    <Td>
                        <Menu>
                            <MenuButton as={Button} rightIcon={<ChevronDownIcon/>}>
                                Actions
                            </MenuButton>
                            <MenuList>
                                <MenuItem>View Description</MenuItem>
                                <MenuItem color={'red'} onClick={() => removeMeal(meal)}>Delete</MenuItem>
                            </MenuList>
                        </Menu>
                        {/* <Button color={'red'} onClick={() => removeMeal(meal)}>Remove</Button> */}
                    </Td>
                </Tr>
            ))
            }
            
        </Tbody>
        </Table>
        </TableContainer>
    : 
        <Text>
            Please add a meal
        </Text>
    }
    </Box>
  );
}

export default MealForm;