import * as React from "react"
import {useState} from "react"
import {
  Box,
  Heading,
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  Text,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalCloseButton,
  ModalBody,
  Button,
  TableContainer,
} from '@chakra-ui/react';
import { ChevronDownIcon } from '@chakra-ui/icons';
import { Meal } from './Meal';

const days = ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'];
const meals = ['Breakfast', 'Lunch', 'Dinner'];

function WeekMeals({mealList}) {
    // This should be 2D array, each day * number of meals.
    // This is creating array length 7 iwth objects initialised with empty strings with 3 fields.
    // day x meal. 

    // setSelectedMeals needs to accept the 2d array with the change
    // const [selectedMeals, setSelectedMeals] = useState(
    //   Array.from({ length: days.length }, () => Array.from({length: meals.length}, () => ''))
    // );

    const [selectedMeals, setSelectedMeals] = useState<Meal[]>(() => {
      const saved = localStorage.getItem("savedWeekMeals");
      if (saved == null) { // Type guard against null
        return Array.from({ length: days.length }, () => Array.from({length: meals.length}, () => ''))
      }
      const initialValue = JSON.parse(saved);
      return initialValue;
    });


    const handleMealSelect = (day, meal, mealChoice) => {
      let copy = [...selectedMeals];
      copy[day][meal] = mealChoice.name 
      setSelectedMeals(copy)
      // setIsOpen(false)
      localStorage.setItem("savedWeekMeals", JSON.stringify(copy));
    };

    return (
      <Box
      p={'5'}
      // boxShadow={'16px 16px 23px #a1a1a1,-16px -16px 23px #ffffff;'}
      borderRadius='20px'
      border={'thin solid'}
      borderColor={'#65B741'}
      >
        
        <Heading as='h3' textAlign={'center'} paddingBottom={'5'}>
          Your Week
        </Heading>
        <TableContainer overflowX={'auto'} maxHeight={'300px'}>
        <Table>
          <Thead>
            <Tr>
              <Th>Meal</Th>
              {days.map((day, index) => (
                <Th key={index}>{day}</Th>
              ))}
            </Tr>
          </Thead>
          <Tbody>
            {meals.map((meal, mealIndex) => (
              <Tr key={mealIndex}>
                <Td>{meal}</Td>
                {days.map((day, dayIndex) => (
                  <Td key={dayIndex}>
                      <Menu>
                        <MenuButton as={Button} rightIcon={<ChevronDownIcon/>}>
                          {selectedMeals[dayIndex][mealIndex] || 'Select Meal'}
                        </MenuButton>
                        <MenuList>
                        {mealList.length == 0 ? <Text> Please add a meal</Text> : 
                          mealList.map((mealChoice, index) => (
                            <Box key={index} mb={2}>
                                <MenuItem onClick={() => handleMealSelect(dayIndex, mealIndex, mealChoice)} as={Button}>{mealChoice.name}</MenuItem>
                            </Box>
                          ))
                        }
                        </MenuList>
                    </Menu>
                  </Td>
                ))}
              </Tr>
            ))}
          </Tbody>
        </Table>
        </TableContainer>
      </Box>
    );
  }
  

export default WeekMeals

